<template>
    <div class="search-page">
        <div class="search-wrap">
            <form action="" class="input-kw-form">
                <input class="search" type="search" v-model="searchText" placeholder="请输入关键字搜索" autocomplete="off" v-focus="focusStatus" @keypress="_onKeypress">
            </form>
        </div>
        <div class="search-content">
            <ul class="list">
                <li class="item" v-for="(x, index) in list" :key="index" @click="_search(x)">
                    {{x.name}}
                </li>
            </ul>
            <div class="clear">
                <i class="logo"></i>
                <span class="text">清空历史记录</span>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'search',
    data() {
        return {
            list: [
                {
                    name: '上衣',
                },
                {
                    name: '裤子',
                },
            ],
            focusStatus: true,
            searchText: '',
        };
    },
    created() {
    },
    mounted() {
    },
    methods: {
        _onKeypress(e) {
            let keycode = e.keyCode;
            if (keycode === 13) {
                e.preventDefault();
                this.$router.push({ path: '/product-select', query: { text: this.searchText }});
            }            
        },
        _search(x) {
            this.$router.push({ path: '/product-select', query: { text: x.name }});
        },
    }
}
</script>

<style lang="stylus" scoped>
.search-page {
    font-family:HiraginoSansGB-W3;
    background: #F2F2F2;
    width: 100%;
    height: 100%;
    .search-wrap {
        background: #FFFFFF;
        text-align: center;
        padding: 12px 0;
        .input-kw-form {
            width: 700px;
            margin: 0 auto;
        }
        .search {
            width: 700px;
            height: 60px;
            line-height: 1rem;
            background: #F2F2F2;
            border-radius: 30px;
            padding-left: 40px;
            font-size: 24px;
            color: #666666;
        }
    }
    .search-content {
        padding: 40px;
        .list {
            .item {
                display: inline-block;
                margin: 0 20px 20px 0;
                padding: 14px 37px;
                background: #FFFFFF;
                border-radius:24px;
                font-size: 24px;
                color: #333333;
                text-align: center;
            }
        }
        .clear {
            margin-top: 65px;
            text-align: center;
            font-size: 0;
            .logo {
                vertical-align: top;
                margin-right: 18px;
				display: inline-block;
				width: 32px;
				height: 32px;
				background-image: url(../../../static/image/common/icon_common_delete@2x.png);
				background-size: 100% 100%;
				background-repeat: no-repeat;
            }
            .text {
                display: inline-block;
                font-size: 24px;
                color: #999999;
                height: 32px;
                line-height: 32px;
            }
        }
    }
}
</style>



